.userName {
  @media screen and (max-width: 320px) {
    font-size: 10px;
  }
  color: $primariy-bcg-color;
  font-weight: 500;
  font-size: 20px;
  background-color: rgb(254, 243, 230);
  padding: 10px;
  border-radius: 5px;
  margin: 0;
}

.burger {
  padding: 6px;
  border-radius: 5px;
  background-color: rgb(254, 243, 230);
  color: $primariy-bcg-color;
  font-size: 30px;
  transition: 300ms linear;
  &:hover {
    color: rgb(254, 243, 230);
    background-color: $primariy-bcg-color;
  }
}
